.confirm-wrapper {
	.confirm-content {
	font-size: 1.25rem;
	color: var(--text-color);
	}
	.btn-group{
		display: flex;
		gap: 8px;
		justify-content: flex-end;
	}
	.btn {
		
		transition: color 0.25s ease;
		padding: 6px 12px;
		background-color: transparent;
		font-size: 0.95rem;
		cursor: pointer;
		border: 2px solid var(--border-color);
		border-radius: 6px;
		&.confirm {
			color: var(--BrightRed);
			&:hover{
				border-color: var(--BrightRed);
			}
		}

		&.demiss {
			color: var(--LimeGreen);
			&:hover{
				border-color: var(--LimeGreen);
			}
		}
	}
}