@import url("https://fonts.googleapis.com/css2?family=Noto+Serif+SC:wght@300;700&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300&display=swap');
@import "normalize.css";

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html *::-webkit-scrollbar {
	border-radius: 0;
	width: 8px;
}
html *::-webkit-scrollbar-thumb {
	border-radius: 4px;
	background-color: rgba(0, 0, 0, 0.15);
}
html *::-webkit-scrollbar-track {
	border-radius: 0;
	background-color: rgba(0, 0, 0, 0);
}
:root {
	--H-height: 40px;
	--LimeGreen: hsl(163, 72%, 41%);
	--BrightRed: hsl(356, 69%, 56%);
	--white: hsl(0, 0%, 100%);
	// light theme
	--background-pri: hsl(225, 100%, 98%);
	--background-sec: hsl(227, 47%, 96%);
	--text-color: hsl(230, 17%, 14%);
	--border-color: hsl(228, 12%, 44%);
	// dark theme
	--VeryDarkBlueD: hsl(230, 17%, 14%);
	--VeryDarkBlueD1: hsl(232, 19%, 15%);
	--DarkDesaturatedBlue: hsl(228, 28%, 20%);
	--DesaturatedBlue: hsl(228, 34%, 66%);
}
.pinyin{
	padding: 3px 6px;
	font-size: 1.6rem;
	color: var(--LimeGreen);
}

body {
	background-color: var(--background-pri);
	color: var(--text-color);
	transition: all 0.5s ease-out;
}
body.dark-mode {
	--background-pri:hsl(230, 17%, 14%);
	--background-sec:hsl(232, 19%, 15%);
	//--text-color: hsl(228, 34%, 66%);
	--text-color:var(--white);
	--border-color: hsl(228, 28%, 20%);
	transition: all 0.5s ease-out;
	background-color: var(--background-pri);
	color: var(--text-color);
}



.react-contextmenu-wrapper{
	flex-grow: 1;
}

// file default style
.text-red {color: #b91c1c;}
.text-blue {color : #1d4ed8}
.text-green{color: #15803d;}
.card {
	min-width: 80px;
    min-height: 80px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: var(--background-sec);
	box-shadow: rgb(0 0 0 / 12%) 0px 1px 6px;
    border: 2px solid transparent;
	transition: all 0.25s ease;
    border-radius: 6px;
	&:hover{
		  background-color: var(--background-pri);
	}
}

.textarea {
			font-family: "Noto Serif SC", serif;
			user-select: none;
			-webkit-appearance: none;
			appearance: none;
			background-color: var(--background-pri);
			width: 100%;
			padding:8px 16px;
			color: inherit;
			font-size: 1.25rem;
			cursor: text;
			border: 1px solid var(--border-color);
			border-radius: 6px;
			outline: 1px solid transparent;
			&:focus {
				outline-color: var(--LimeGreen);
			}
}

input[type=range] {
	appearance:none;
  -webkit-appearance: none;
  width: 100%;
  background-color: transparent;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 12px;
  cursor: pointer;
  background: var(--background-sec);
  border-radius: 6px;
  border: 1px solid var(--border-color);
}
input[type=range]::-webkit-range-track {
  height: 6px;
  width: 6px;
  border-radius: 6px;
  background: var(--LimeGreen);
  cursor: pointer;
}
input[type=range]:focus::-webkit-slider-runnable-track {
  background: var(--background-sec);

}