.wrapper{
    flex-grow: 1;
    padding: 8px 16px;
	display: flex;
	flex-direction: column;
	gap: 8px;
}
.list-wrapper {
    list-style: none;
	display: flex;
	flex-direction: column;
	max-height:70% ;
	overflow: overlay;
	gap: 8px;
}
.item-wapper{
	border: 2px solid var(--border-color);
	border-radius: 3px;
}
.active{
	border: 2px solid var(--LimeGreen);
}
.item {
	padding:6px 8px;
    display: block;
	background: var(--background-sec);
	cursor: pointer;
	transition: background 250ms linear, color 250ms linear;
	font-size: 0.75rem; 
	&:hover {
		background-color: var(--border-color);
		color: var(--LimeGreen);
	}
}
