.App-main {
	display: flex;
	flex-direction: column-reverse;
	margin: 0 auto;
	margin-top: var(--H-height);
	max-height: calc(100vh - var(--H-height));
	height: calc(100vh - var(--H-height));
	padding: 0 15px;
	gap: 15px;
}
.App-content {
	position: relative;
	display: flex;
	flex-grow: 1;
	border: 2px solid var(--border-color);
	border-radius: 6px;
	overflow: overlay;
}

.App-sidebar {
	margin-left: auto;
	display:none;
	overflow: hidden;
	height: 100%;
	flex-shrink: 0;
	transition: width 0.5s ease;
}
.showSidebar {
	display: block;
}

.setting-icon{transition: color 0.5s ease;&:hover{color: var(--LimeGreen);}}
.onMobile-toggle-sisebar-btn {
	appearance: none;
	background-color: transparent;
	border: 2px dotted var(--border-color);
	color: inherit;
	font-size: 1rem;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 5px;
	border-radius: 5px;
	cursor: pointer;
     &:active{
         border-color: var(--LimeGreen); 
     }
}
@media screen and (min-width: 920px) {
	.App-main {
		flex-direction: row;
		margin-top:0;
		max-height:100vh;
		height: 100vh;
	}
     //default open on pc
	.App-sidebar {
		display: block;
	}
	.onMobile-toggle-sisebar-btn {
		display: none;
	}
}

// css input elements

.set-input:not([type="checkbox"]) {
	-webkit-appearance: none;
	appearance: none;
	background-color: transparent;
	border: none;
	border-bottom: 2px dotted var(--border-color);
	color: inherit;
	font-size: 1rem;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
	-webkit-appearance: none;
}
input[type="color"] {
	-webkit-appearance: none;
	//border: none;
	border-radius: 6px;
	width: 32px;
	height: 32px;
}
input[type="color"]::-webkit-color-swatch-wrapper {
	padding: 0;
}
input[type="color"]::-webkit-color-swatch {
	//border: none;
	border-radius: 6px;
}

.set-input:invalid {
	color: tomato;
}
.set-input:focus {
	outline: none;
	border-color: var(--LimeGreen);
}
