.wrapper {
	padding-top: 15px;
	height: inherit;
	border-left: 2px solid var(--border-color);
	display: flex;
	gap: 8px;
	flex-direction: column;
	overflow: overlay;
	user-select: none;
	.sidebar_header {
		text-align: center;
		padding: 0 15px;
		.inputfile {
			width: 0.1px;
			height: 0.1px;
			opacity: 0;
			overflow: hidden;
			position: absolute;
			z-index: -1;
		}
		.inputfile + label {
			font-size: 1em;
			font-weight: 700;
			color: white;
			background-color: black;
			display: inline-block;
		}

		.inputfile:focus + label,
		.inputfile + label:hover {
			background-color: var(--background-sec);
		}
		.inputfile + label {
			cursor: pointer; /* "hand" cursor */
		}
	}
	.sidebar_body {
		display: flex;
		flex-direction: row-reverse;
		flex-grow: 1;
	}
}

.export-wrapper {
	margin-top: auto;
	button {
		transition: color 0.5s ease;
		background-color: transparent;
		font-size: 1rem;
		min-height: 16px;
		cursor: pointer;
		padding: 3px 6px;
		border: none;
		border-radius: 3px;
		border-bottom: 2px solid var(--border-color);
		color: inherit;
		&:hover {
			color: var(--LimeGreen);
		}
	}
}
.tab_btnStyle {
	transition: color 0.5s ease;
	background-color: transparent;
	font-size: 1rem;
	min-height: 16px;
	cursor: pointer;
	padding: 3px 6px;
	border: none;
	border-radius: 3px;
	border-bottom: 2px solid var(--border-color);
	color: inherit;

	&:hover {
		color: var(--LimeGreen);
	}
	&[tabindex]:focus {
		border-color: transparent;
		outline: 2px var(--LimeGreen) solid;
	}
	&.tabActive {
		border-bottom-color: var(--LimeGreen);
	}
}

.context_menu_warpper {
	background-color: var(--background-sec);
	border: 1px solid var(--border-color);
	border-radius: 6px;
	z-index: 999;
}
.context_menu_item {
	padding: 6px 12px;
	cursor: pointer;
	transition: background 250ms linear, color 250ms linear;
	border-radius: 3px;
	font-size: 0.95rem;
	span {
		margin-left: 8px;
		text-transform: capitalize;
		font-size: 0.75rem;
		color: var(--text-color);
	}
	&:not(:last-child) {
		margin-bottom: 3px;
		border-bottom: 1px solid rgb(0 0 0 / 12%);
	}
	&:hover {
		background-color: var(--border-color);
		color: var(--white);
	}
}

.input-wrraper {
	padding: 4px 8px;
	width: 88%;
	margin: 0 auto;
	.input {
		-webkit-appearance: none;
		appearance: none;
		background-color: transparent;
		font-size: 1rem;
		font-weight: 300;
		border: 2px solid var(--border-color);
		padding: 4px 8px;
		flex-grow: 1;
		width: 100%;
		height: 100%;
		color: inherit;
		border-radius: 4px;
		outline: 2px transparent solid;
		&[tabindex]:focus {
			outline-color: var(--LimeGreen);
		}
	}
}
