.header{
    display: block;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: var(--background-sec);
    z-index: 999;
    .wrapper{
        padding: 8px 16px;
        height: var(--H-height);
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .heading{
        font-family: 'Noto Serif SC', serif;
        font-size: 1.45rem;
        font-weight: 700;
        margin-right: auto;
    }
}

@media screen and (min-width: 920px) {
	.header {
        display: none;	
    }
}